.mb-8 {
  margin-bottom: 6rem;
}

.banner {
  position: relative;
  background: linear-gradient(110deg, #ffc000 60%, #ffed4b 60%);
  color: #fff;
}

h1 {
  font-weight: 700;
  line-height: 1.3;
  font-size: 3.5rem;
}

section {
  padding: 5rem 0;
}

blockquote {
  font-size: 1.25rem;
  font-weight: 600;
  border-left: 3px solid #fff;
  padding-left: 30px;
}

@media (max-width: 767.98px) {
  h1 {
    font-size: calc(1.575rem + 3.9vw);
  }
}
